import { defineComponent, reactive } from 'vue';
import { rules } from '../config/phone-config';
export default defineComponent({
  setup() {
    const phone = reactive({
      num: '',
      code: ''
    });

    const getcode = () => {
      let str = '';

      for (let i = 0; i < 6; i++) {
        str += Math.floor(Math.random() * 10);
      }

      alert(str);
    };

    return {
      phone,
      rules,
      getcode
    };
  }

});